'use client';

import { useEffect, useState } from 'react';
import axios from 'axios';
// @/components/ReviewModal 경로가 설정되어 있지 않다면 상대 경로 사용
import ReviewModal from '@/components/ReviewModal'; 

// 예약 데이터 타입 정의
interface Reservation {
  id: string;
  startDate: string;
  endDate: string;
  totalPrice: number;
  status: 'PENDING' | 'CONFIRMED' | 'CANCELLED';
  caravan: {
    id: string;
    name: string;
    location: string;
    images: string[];
  };
}

export default function MyReservationsPage() {
  const [reservations, setReservations] = useState<Reservation[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');
  
  // 리뷰 모달 상태 관리
  const [selectedReservation, setSelectedReservation] = useState<Reservation | null>(null);
  const [isReviewModalOpen, setIsReviewModalOpen] = useState(false);

  // 데이터 불러오기 함수
  const fetchReservations = async () => {
    try {
      const userStr = localStorage.getItem('user');
      if (!userStr) {
        setError('로그인이 필요합니다.');
        if (typeof window !== 'undefined') {
          window.location.href = `${window.location.origin}/auth/login`;
        }
        return;
      }

      const user = JSON.parse(userStr);
      // 127.0.0.1 사용 (연결 안정성)
      const response = await axios.get(`http://127.0.0.1:3001/api/reservations/user/${user.id}`);
      setReservations(response.data);
    } catch (err: any) {
      console.error('예약 불러오기 실패:', err);
      setError('예약 내역을 불러오는데 실패했습니다.');
    } finally {
      setLoading(false);
    }
  };

  // 컴포넌트 마운트 시 데이터 조회
  useEffect(() => {
    fetchReservations();
  }, []);

  // 예약 취소 핸들러
  const handleCancelReservation = async (reservationId: string) => {
    if (!confirm('정말로 예약을 취소하시겠습니까?\n취소 시 환불 정책에 따라 처리됩니다.')) return;

    try {
      await axios.patch(`http://127.0.0.1:3001/api/reservations/${reservationId}/status`, {
        status: 'CANCELLED',
      });
      alert('예약이 취소되었습니다.');
      fetchReservations(); // 목록 새로고침
    } catch (err) {
      console.error(err);
      alert('예약 취소 중 오류가 발생했습니다.');
    }
  };

  // 리뷰 모달 열기 핸들러
  const openReviewModal = (reservation: Reservation) => {
    setSelectedReservation(reservation);
    setIsReviewModalOpen(true);
  };

  // 날짜 포맷팅 (YYYY. M. D.)
  const formatDate = (dateString: string) => {
    return new Date(dateString).toLocaleDateString('ko-KR', {
      year: 'numeric', month: 'numeric', day: 'numeric'
    });
  };

  // 가격 포맷팅 (₩100,000)
  const formatPrice = (price: number) => {
    return new Intl.NumberFormat('ko-KR', { style: 'currency', currency: 'KRW' }).format(price);
  };

  if (loading) return <div className="p-10 text-center text-gray-500">로딩 중...</div>;

  return (
    <div className="max-w-5xl mx-auto px-4 sm:px-6 lg:px-8 py-10">
      <h1 className="text-2xl font-bold text-gray-900 mb-8">내 예약 목록</h1>

      {error && <div className="text-red-500 text-center py-10">{error}</div>}

      {reservations.length === 0 && !error ? (
        <div className="text-center py-20 bg-gray-50 rounded-lg border-2 border-dashed border-gray-300">
          <p className="text-gray-500 mb-4">아직 예약된 여행이 없습니다.</p>
          <a href="/caravans" className="text-indigo-600 font-medium hover:underline">카라반 구경하러 가기 &rarr;</a>
        </div>
      ) : (
        <div className="space-y-6">
          {reservations.map((res) => {
            // 이용 완료 여부 체크 (오늘 날짜가 체크아웃 날짜 이후인지)
            const endDate = new Date(res.endDate);
            const now = new Date();
            const isCompleted = now >= endDate && res.status === 'CONFIRMED';

            return (
              <div key={res.id} className="bg-white border border-gray-200 rounded-xl p-6 shadow-sm hover:shadow-md transition-shadow duration-200">
                
                <div className="flex flex-col md:flex-row justify-between items-start md:items-center gap-4">
                  {/* 왼쪽: 예약 정보 */}
                  <div className="flex-1">
                    <div className="flex items-center gap-3 mb-2">
                      <h3 className="text-lg font-bold text-gray-900">{res.caravan.name}</h3>
                      {/* 상태 뱃지 */}
                      <span className={`text-xs px-2.5 py-0.5 rounded-full font-semibold border
                        ${res.status === 'CONFIRMED' ? 'bg-green-50 text-green-700 border-green-200' : 
                          res.status === 'CANCELLED' ? 'bg-gray-100 text-gray-500 border-gray-200' : 'bg-yellow-50 text-yellow-700 border-yellow-200'}`}>
                        {res.status === 'CONFIRMED' ? (isCompleted ? '이용 완료' : '예약 확정') : 
                         res.status === 'CANCELLED' ? '취소됨' : '승인 대기'}
                      </span>
                    </div>
                    
                    <p className="text-sm text-gray-500 mb-4">{res.caravan.location}</p>
                    
                    <div className="flex flex-wrap gap-x-8 gap-y-2 text-sm">
                      <div>
                        <span className="text-xs text-gray-400 block mb-0.5">체크인</span>
                        <span className="font-medium text-gray-800">{formatDate(res.startDate)}</span>
                      </div>
                      <div>
                        <span className="text-xs text-gray-400 block mb-0.5">체크아웃</span>
                        <span className="font-medium text-gray-800">{formatDate(res.endDate)}</span>
                      </div>
                      <div>
                        <span className="text-xs text-gray-400 block mb-0.5">결제 금액</span>
                        <span className="font-bold text-indigo-600">{formatPrice(res.totalPrice)}</span>
                      </div>
                    </div>
                  </div>

                  {/* 오른쪽: 버튼 액션 */}
                  <div className="w-full md:w-auto flex justify-end mt-4 md:mt-0">
                    {/* 1. 여행 완료 시 -> 리뷰 쓰기 버튼 */}
                    {isCompleted && (
                      <button 
                        onClick={() => openReviewModal(res)}
                        className="px-5 py-2 bg-indigo-600 text-white text-sm font-bold rounded-lg hover:bg-indigo-700 transition shadow-sm flex items-center gap-2"
                      >
                        <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24"><path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M11 5H6a2 2 0 00-2 2v11a2 2 0 002 2h11a2 2 0 002-2v-5m-1.414-9.414a2 2 0 112.828 2.828L11.828 15H9v-2.828l8.586-8.586z"></path></svg>
                        리뷰 쓰기
                      </button>
                    )}

                    {/* 2. 아직 이용 전이거나 대기 중 -> 예약 취소 버튼 */}
                    {(res.status === 'PENDING' || (res.status === 'CONFIRMED' && !isCompleted)) && (
                      <button 
                        onClick={() => handleCancelReservation(res.id)}
                        className="px-4 py-2 bg-red-500 text-white text-sm font-bold rounded-lg hover:bg-red-600 transition shadow-sm"
                      >
                        예약 취소
                      </button>
                    )}

                    {/* 3. 취소된 예약 -> 텍스트 표시 */}
                    {res.status === 'CANCELLED' && (
                      <span className="px-4 py-2 bg-gray-100 text-gray-400 text-sm font-medium rounded-lg cursor-not-allowed">
                        취소된 예약
                      </span>
                    )}
                  </div>
                </div>
              </div>
            );
          })}
        </div>
      )}

      {/* 리뷰 작성 모달 연결 */}
      {selectedReservation && (
        <ReviewModal 
          isOpen={isReviewModalOpen}
          onClose={() => setIsReviewModalOpen(false)}
          caravanId={selectedReservation.caravan.id}
          caravanName={selectedReservation.caravan.name}
          onSuccess={fetchReservations}
        />
      )}
    </div>
  );
}